.menu-toggle {
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 50%;
  outline: none;
  align-items: center;
  width: 50px;
  min-width: 40px;
  height: 50px;
  min-height: 40px;
  margin: 0;
  padding: 0;
  display: flex;
}

.menu-container {
  z-index: 999;
  background-color: #1e1823;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 236px;
  height: 100%;
  padding-top: 150px;
  font-size: 16px;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
}

.mobile-menu {
  flex-direction: column;
  gap: 24px;
  padding-left: 5px;
  padding-right: 30px;
  list-style: none;
  display: inline-flex;
}

.mobile-menu .link:after {
  content: "";
  background-color: #fd9222;
  width: 0;
  height: .3px;
  margin: auto;
  display: flex;
  position: absolute;
}

.mobile-menu .link:hover:after {
  width: 100%;
  transition: width .3s linear;
}

.mobile-menu .link:not(:hover):after {
  width: 0;
  transition: width .3s ease-out;
}

.mobile-menu .link {
  color: #fff;
  letter-spacing: .72px;
  padding-left: 45px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.mobile-menu .link:hover {
  color: #fd9222;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.icons-toggle {
  flex-direction: row;
  gap: 18px;
  padding-bottom: 24px;
  padding-left: 32px;
  padding-right: 150px;
  list-style: none;
  display: flex;
}

.toggle-svg:hover {
  filter: brightness(0) saturate() invert(71%) sepia(43%) saturate(4333%) hue-rotate(344deg) brightness(104%) contrast(98%);
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

.js-close-button {
  cursor: pointer;
  background-color: #0000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 24px;
  right: 24px;
}

.js-close-button:hover {
  filter: brightness(0) saturate() invert(71%) sepia(43%) saturate(4333%) hue-rotate(344deg) brightness(104%) contrast(98%);
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

